"use client";

import { useState } from "react";
import {
  Listbox,
  ListboxButton,
  ListboxOption,
  ListboxOptions,
} from "@headlessui/react";
import { ChevronDownIcon } from "@heroicons/react/16/solid";
import { CheckIcon } from "@heroicons/react/20/solid";
import { Formik, Form, Field, ErrorMessage } from 'formik';

const audience = [
  { id: 1, name: "Software Engineer" },
  { id: 2, name: "HR" },
];

const eventType = [
  { id: 1, name: "All" },
  { id: 2, name: "Managers only" },
];

export default function App() {
  const [audienceSelected, audienceSetSelected] = useState(null);
  const [eventTypeSelected, eventTypeSetSelected] = useState(null);

  return (
    <>
     <Formik
  initialValues={{ 
    title: '', 
        actionBtn: '',
        url:'',
        resurl: '', 
        audience: '',
        project:'',
        type:'',
        setdate:'',
        expiredate:'',
        banner:'null'
  }}
  validate={values => {
    const errors = {};
    if (!values.title) {
      errors.title = 'Enter title';
    }
    if (!values.btext) {
      errors.btext = 'Enter Action Button text';
    }
    if (!values.url) {
      errors.url = 'Enter URL of the form(Eg: Google form)';
    }
    if(!values.resurl){
      errors.resurl = 'Enter Response URL of the form';
    }
    if(!values.audience){
      errors.resurl = 'Select a audience';
    }
    return errors;
  }}
  onSubmit={(values, { setSubmitting }) => {
    setTimeout(() => {
      alert(JSON.stringify(values, null, 2));
      setSubmitting(false);
    }, 400);
  }}
>
  {({ values, errors, touched, handleChange, handleBlur, isSubmitting }) => (
    <Form>
      <div className="grid grid-cols-1 gap-x-6 gap-y-5 sm:grid-cols-6">
      <div className="sm:col-span-3">
      
        </div>
        
        
        <div className="sm:col-span-3">
          
        <Field
        id="title"
        name="title"
        type="text"
        placeholder={errors.title && touched.title ? errors.title : "Title"}
        className={`block w-full bg-white px-1 text-base text-gray-900 border-b-2 border-gray-300 placeholder:text-gray-400 focus:outline-none focus:placeholder-(color:--my1) focus:border-(color:--my1) sm:text-sm/6 ${
          errors.title && touched.title ? 'placeholder:text-red-500' : ''
        }`}
      />
        
        </div>
        <div className="sm:col-span-3">
              <Field
                id="btext"
                name="btext"
                type="text"
                placeholder={errors.btext && touched.btext ? errors.btext : "Action Button Text"}
        className={`block w-full bg-white px-1 text-base text-gray-900 border-b-2 border-gray-300 placeholder:text-gray-400 focus:outline-none focus:placeholder-(color:--my1) focus:border-(color:--my1) sm:text-sm/6 ${
          errors.btext && touched.btext ? 'placeholder:text-red-500' : ''
        }`}
                />
        </div>

        <div className="sm:col-span-full mt-2 mb-2">
            <div className="flex items-center gap-x-3">
              <div className="group grid  grid-cols-1">
                <input
                  id="showtitle"
                  name="showtitle"
                  type="checkbox"
                  aria-describedby="candidates-description"
                  className="col-start-1 row-start-1 appearance-none rounded-sm border border-gray-300 bg-white checked:border-(color:--my1) checked:bg-(color:--my1)  focus-visible:outline-2 focus-visible:outline-offset-2  disabled:border-gray-300 disabled:bg-gray-100 disabled:checked:bg-gray-100 forced-colors:appearance-auto"
                />
                <svg
                  fill="none"
                  viewBox="0 0 14 14"
                  className="pointer-events-none col-start-1 row-start-1 size-3.5 self-center justify-self-center stroke-white group-has-disabled:stroke-gray-950/25"
                >
                  <path
                    d="M3 8L6 11L11 3.5"
                    strokeWidth={2}
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="opacity-0 group-has-checked:opacity-100"
                  />
                </svg>
              </div>
              <div className="text-sm/6">
                <label
                  htmlFor="showtitle"
                  className="font-medium text-gray-900"
                >
                  Show title on the Event Banner
                </label>
              </div>
            </div>
          </div>

          <div className="sm:col-span-full">
            <Field
              id="url"
              name="url"
              type="text"
              placeholder={errors.url && touched.url ? errors.url : "URL of the form(Eg: Google form)"}
        className={`block w-full bg-white px-1 py-1.5 text-base text-gray-900 border-b-2 border-gray-300 placeholder:text-gray-400 focus:outline-none focus:placeholder-(color:--my1) focus:border-(color:--my1) sm:text-sm/6 ${
          errors.url && touched.url ? 'placeholder:text-red-500' : ''
        }`}
            />
          </div>

          <div className="sm:col-span-full">
            <Field
              id="resurl"
              name="resurl"
              type="text"
              placeholder={errors.resurl && touched.resurl ? errors.resurl : "Response URL of the form"}
        className={`block w-full bg-white px-1 py-1.5 text-base text-gray-900 border-b-2 border-gray-300 placeholder:text-gray-400 focus:outline-none focus:placeholder-(color:--my1) focus:border-(color:--my1) sm:text-sm/6 ${
          errors.resurl && touched.resurl ? 'placeholder:text-red-500' : ''
        }`}
             />
          </div>

          <div className="sm:col-span-full">
          <Listbox
              value={values.audience}
              onChange={(val) => setFieldValue("audience", val)}
            >
              <div className="relative mt-2">
                <ListboxButton
                  className={`grid w-full cursor-pointer grid-cols-1 bg-white pb-1.5 px-1 text-left text-gray-900 border-b-2 border-gray-300 focus:outline-none sm:text-sm ${
                    errors.audience && touched.audience ? "border-red-500" : "focus:border-[--my1]"
                  }`}
                >
                  <span className="col-start-1 row-start-1 flex items-center gap-3 pr-6">
                    <span
                      className={`block truncate ${
                        !values.audience ? "text-gray-400" : ""
                      }`}
                    >
                      {values.audience ? values.audience.name : "Select Audience"}
                    </span>
                  </span>
                  <ChevronDownIcon
                    aria-hidden="true"
                    className="col-start-1 row-start-1 size-5 self-center justify-self-end text-gray-500 sm:size-4"
                  />
                </ListboxButton>

                <ListboxOptions className="absolute z-20 mt-1 w-full rounded-md bg-white py-1 text-base ring-1 shadow-lg ring-black/5 focus:outline-none sm:text-sm">
                  {audience.map((option) => (
                    <ListboxOption
                      key={option.id}
                      value={option}
                      className="group relative cursor-pointer py-2 pr-9 pl-3 text-gray-900 select-none data-focus:bg-gray-400 data-focus:text-white"
                    >
                      <div className="flex items-center">
                        <span className="ml-3 block truncate font-normal group-data-selected:font-semibold">
                          {option.name}
                        </span>
                      </div>
                      <span className="absolute inset-y-0 right-0 flex items-center pr-4 text-gray-600 group-not-data-selected:hidden group-data-focus:text-white">
                        <CheckIcon aria-hidden="true" className="size-5" />
                      </span>
                    </ListboxOption>
                  ))}
                </ListboxOptions>
              </div>
            </Listbox>
            <ErrorMessage name="audience" component="div" className="text-red-500 text-sm mt-1" />
          </div>


      </div>

      <button 
        type="submit" 
        disabled={isSubmitting}
        className="w-full bg-blue-500 text-white py-2 rounded-md hover:bg-blue-600"
      >
        Submit
      </button>
    </Form>
  )}
</Formik>


      <form className="p-5">
        <div className="grid grid-cols-1 gap-x-6 gap-y-5 sm:grid-cols-6">
        

         

          
          

          <div className="sm:col-span-full">
            <input
              id="url"
              name="url"
              type="text"
              placeholder="Select Projects"
              autoComplete="given-name"
              className="block w-full bg-white py-1.5 px-1 text-base text-gray-900 border-b-2 border-gray-300 placeholder:text-gray-400 focus:outline-none focus:placeholder-(color:--my1) focus:border-(color:--my1) sm:text-sm/6"
            />
          </div>

          <div className="sm:col-span-full">
            <Listbox value={eventTypeSelected} onChange={eventTypeSetSelected}>
              <div className="relative mt-2">
                <ListboxButton className="grid w-full cursor-default grid-cols-1 bg-white pb-1.5 px-1 text-left text-gray-900 border-b-2 border-gray-300 focus:outline-none  focus:border-(color:--my1) sm:text-sm">
                  <span className="col-start-1 row-start-1 flex items-center gap-3 pr-6">
                    <span
                      className={`block truncate ${
                        !eventTypeSelected ? "text-gray-400" : ""
                      }`}
                    >
                      {eventTypeSelected
                        ? eventTypeSelected.name
                        : "Event Type"}
                    </span>
                  </span>
                  <ChevronDownIcon
                    aria-hidden="true"
                    className="col-start-1 row-start-1 size-5 self-center justify-self-end text-gray-500 sm:size-4"
                  />
                </ListboxButton>

                <ListboxOptions className="absolute z-20 mt-1 w-full rounded-md bg-white py-1 text-base ring-1 shadow-lg ring-black/5 focus:outline-hidden sm:text-sm">
                  {eventType.map((person) => (
                    <ListboxOption
                      key={person.id}
                      value={person}
                      className="group relative cursor-default py-2 pr-9 pl-3 text-gray-900 select-none data-focus:bg-gray-400 data-focus:text-white"
                    >
                      <div className="flex items-center">
                        <span className="ml-3 block truncate font-normal group-data-selected:font-semibold">
                          {person.name}
                        </span>
                      </div>
                      <span className="absolute inset-y-0 right-0 flex items-center pr-4 text-gray-600 group-not-data-selected:hidden group-data-focus:text-white">
                        <CheckIcon aria-hidden="true" className="size-5" />
                      </span>
                    </ListboxOption>
                  ))}
                </ListboxOptions>
              </div>
            </Listbox>
          </div>

          <div className="sm:col-span-3">
            <input
              id="btext"
              name="btext"
              type="text"
              onFocus={(e) => (e.target.type = "date")}
              onBlur={(e) => (e.target.type = "text")}
              placeholder="Select a Date"
              autoComplete="off"
              className="block p-1 w-full bg-white px-1 text-base text-gray-900 border-b-2 border-gray-300 placeholder-gray-400 focus:outline-none focus:border-[--my1] sm:text-sm pr-10 appearance-none"
            />
          </div>

          <div className="sm:col-span-3">
            <input
              id="btext"
              name="btext"
              type="text"
              onFocus={(e) => (e.target.type = "date")}
              onBlur={(e) => (e.target.type = "text")}
              placeholder="Select a Date"
              autoComplete="off"
              className="block p-1 w-full bg-white px-1 text-base text-gray-900 border-b-2 border-gray-300 placeholder-gray-400 focus:outline-none focus:border-[--my1] sm:text-sm pr-10 appearance-none"
            />
          </div>

          <div className="sm:col-span-full">
            <div className="flex justify-end">
              <input
                type="file"
                id="banner"
                name="banner"
                accept="image/*"
                onChange={(e) => {
                  const fileName = e.target.files.length
                    ? e.target.files[0].name
                    : "Upload Banner";
                  document.getElementById("banner-label").innerText = fileName;
                }}
                className="hidden"
              />
              <label
                id="banner-label"
                htmlFor="banner"
                className="flex bg-white border border-gray-900 px-4 py-2 rounded-md text-gray-600 text-center cursor-pointer hover:bg-gray-100"
              >
                <img
                  src="/upload.png"
                  alt="Upload Icon"
                  className="w-5 h-5 mr-3"
                />
                Upload Banner
              </label>
            </div>
          </div>

          <div className="sm:col-span-full">
            <div className="px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
              <button
                type="button"
                onClick={() => setOpenSubmit(true)}
                className="inline-flex w-full justify-center rounded-md px-3 py-2 text-sm font-semibold text-(color:--my1) shadow-xs hover:bg-gray-100 sm:ml-3 sm:w-auto"
              >
                Add
              </button>
              <button
                type="button"
                data-autofocus
                onClick={() => setOpenForm(false)}
                className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900  shadow-xs ring-gray-300 ring-inset hover:bg-gray-100 sm:mt-0 sm:w-auto"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      </form>
    </>
  );
}
